import { Component, OnInit } from '@angular/core';
import { DataServiceService } from '../data-service.service';
import { Item } from '../model/item';

@Component({
  selector: 'app-shopping-item',
  templateUrl: './shopping-item.component.html',
  styleUrls: ['./shopping-item.component.scss']
})
export class ShoppingItemComponent implements OnInit {
  shoppingItemsList: Item[] = [];
  updateFlag: boolean = false;
  updateId: string;
  constructor(private _dataService: DataServiceService) { }

  ngOnInit() {
    this.getItems();
  }
  getItems(){
      this._dataService.getItems().subscribe((items) => {
        this.shoppingItemsList = items;
      })
  }
  deleteItem(itemId: string){
    this._dataService.deleteItem(itemId).subscribe((response) => {
      this.getItems();
    })
  }
  addItem(itemName, itemQuantity){
    let itemBought: boolean;
    var element = document.getElementsByName('bought');
    if(element[0]['checked']){
      itemBought = true
    }else if(element[1]['checked']){
      itemBought = false
    }
    this._dataService.addShoppingItem(
      { itemName: itemName.value, itemQuantity: itemQuantity.value, itemBought }).subscribe((data) => {
        this.getItems();
      })
  }
  update(id: string){
    this.updateFlag = true;
    this.updateId = id;
  }

  updateItem(itemName: string, itemQuantity: number){
    let updateObj = {itemName: itemName['value'], itemQuantity: itemQuantity['value']};
    this._dataService.updateItem(this.updateId, updateObj).subscribe((data) => {
      console.log(data)
      this.getItems();
    });
  } 
}
