import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Item } from './model/item';

@Injectable({
  providedIn: 'root'
})
export class DataServiceService {

  constructor(private _http: HttpClient) { }
  getItems(): Observable<Item[]>{
    let getURL = 'shops/items';
    return this._http.get<Item[]>(getURL);
  }

  addShoppingItem(newItem: Item){
    let postURL = 'shops/item';
    let header = new HttpHeaders();
    header.append('Content-Type','application/json');
    return this._http.post(postURL, newItem, {headers: header});
  }

  deleteItem(id: string){
    let deleteURL = '/shops/item';
    return this._http.delete(`${deleteURL}/${id}`);
  }

  updateItem(id: string, data){
    let updateURL = 'shops/item';
    let header = new HttpHeaders();
    header.append('Content-Type','application/json');
    return this._http.patch(`${updateURL}/${id}`, data, {headers: header});
  }
}
